.cart-button {
  background: transparent;
  border: none;
  cursor: pointer;
  font-family: 'Roboto Mono', sans-serif;
  font-weight: normal;
  transition: babackground 0.2s ease;

  &_special,
  &_loading,
  &_disable {
    background: #00aa5b;
    color: #fff;
    width: 120px;
    padding: 10px 0;
    font-weight: bold;
    font-size: 16px;
    line-height: 18px;

    &:hover {
      background: #038c4c;
    }
  }

  &_loading {
    color: transparent;
    position: relative;
    cursor: default;
    &:after {
      position: absolute;
      display: block;
      content: '';
      border: 2px solid #d5d5d5;
      border-top: 2px solid #fff;
      border-radius: 50%;
      width: 16px;
      height: 16px;
      left: calc(50% - 8px);
      top: calc(50% - 8px);
      margin-top: -2px;
      animation: spin 1s linear infinite;
    }

    &:hover {
      background: #00aa5b;
    }
  }

  &_disable {
    cursor: not-allowed;

    &:hover {
      background: #00aa5b;
    }
  }

  &_simple {
    background: transparent;
    color: #000;
    width: 50px;
    padding: 10px 0;
    font-weight: normal;
    font-size: 15px;
    line-height: 16px;

    &:hover {
      text-decoration: underline;
    }
  }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@media (max-width: 440px) {
  .cart-button {
    &_special,
    &_disable,
    &_loading {
      padding: 15px 0;
      width: 120px;
    }
  }
}
