.cart-block {
  margin: 80px 0;
  padding: 30px;
  background: #fff;
  border-radius: 5px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: max-content;
  max-width: 940px;

  &__order-img {
    width: 100%;
    max-width: 600px;
    display: block;
    margin: 30px auto 0;
  }

  &__title {
    text-align: center;
    width: 100%;
    font-family: 'Roboto Mono', serif;
    font-size: 28px;
    margin-bottom: 30px;

    &_ordered {
      width: 940px;
      max-width: 100%;
      text-align: center;
      font-family: 'Roboto Mono', serif;
      margin-bottom: 20px;
      color: #00aa5b;
    }
  }

  &__row {
    display: flex;
    align-items: flex-start;
    width: 100%;
  }
}

@media (max-width: 940px) {
  .cart-block {
    padding: 20px;
    margin: 0;
  }
}


@media (max-width: 768px) {
  .cart-block {
    padding: 10px 10px 30px;
    min-height: 100%;
    &__title {
      &_ordered {
        padding-top: 50px;
      }
    }
    &__row {
      flex-direction: column;
    }
  }
}

@media (max-width: 440px) {
  .cart-block {
    padding: 10px 0 30px;
  }
}