.cart-options {
  font-family: 'Roboto Mono', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  &__select {
    background: transparent;
    border: none;
    color: #000000;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    padding: 3px 25px 3px 0;
    background-image: url(../../../../images/arrow.svg);
    background-position: center right;
    background-repeat: no-repeat;
    border-radius: 0;
    border-right: 1px solid #D5D5D5;
    margin-right: 5px;
  }

  &__count {
    cursor: text;
    &__input {
      border: none;
      width: 50px;

      &::-webkit-inner-spin-button, 
      &::-webkit-outer-spin-button {
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
      }
    }
  }
}

@media (max-width: 440px) {
  .cart-options {
    font-size: 13px;
    line-height: 14px;
    &__select {
      padding-right: 20px;
    }
    &__count {
      &__input {
        width: 40px;
      }
    }
  }
}
