.cart-total {
  position: sticky;
  top: 0;
  width: 40%;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__warning {
    width: 100%;
    font-family: 'Roboto Mono', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: #ff0000;
    padding-top: 10px;
  }

  &__price {
    margin-top: 5px;
    align-self: flex-start;
    font-family: 'Roboto Mono', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 22px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}

.tabs {
  width: 100%;
  padding: 30px 0 10px;
  background: rgba(0, 0, 0, 0.03);
  &__nav {
    display: inline-flex;
    margin-bottom: 10px;
    max-width: 100%;
  }

  &__body {
    width: 100%;
    padding: 0 30px;
  }

  &__control {
    width: 100%;
    display: flex;
    align-items: center;

    &__prev {
      margin-right: auto;
    }

    &__next {
      margin-left: auto;
    }
  }

  &__item {
    padding-bottom: 10px;
    position: relative;
    font-family: 'Roboto Slab', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 18px;
    width: 90px;
    margin: 0 15px;
    text-align: center;
    border: none;
    background: transparent;
    transform: scale(0.9);
    opacity: 0.7;
    transition: all 0.2s ease;

    &:after {
      content: '';
      display: block;
      width: 100%;
      height: 1px;
      background: #d5d5d5;
      position: absolute;
      bottom: -1px;
      left: 0;
      opacity: 0;
      transition: all 0.2s ease;
    }

    &:before {
      content: '';
      display: block;
      width: 0;
      height: 1px;
      background: #272727;
      position: absolute;
      top: calc(50% - 4px);
      left: 0;
      transition: width 0.07s ease;
    }

    &_warning {
      color: #ff0000;
    }

    &_active {
      transform: scale(1.1);
      opacity: 1;

      &:after {
        opacity: 1;
      }
    }

    &_disable {
      cursor: default;

      &:before {
        width: 100%;
      }
    }
  }
}

@media (max-width: 940px) {
  .cart-total {
    &__price {
      flex-direction: column;
      align-items: flex-start;
      button {
        margin-top: 10px;
      }
    }
    &__payment {
      justify-content: flex-start;
    }
  }

  .tabs {
    &__nav {
      width: 100%;
      justify-content: space-between;
    }
    &__item {
      font-size: 14px;
      margin: 0;
      &_active {
        transform: scale(1);
      }
    }
    &__body {
      padding: 0 10px;
    }
  }
}

@media (max-width: 768px) {
  .cart-total {
    position: relative;
    margin-top: 30px;
    width: 100%;
    &__price {
      flex-direction: row;
      align-items: center;
      button {
        margin: 0;
      }
    }
    &__payment {
      justify-content: flex-end;
    }
  }

  .tabs {
    &__nav {
      width: 100%;
      justify-content: space-around;
    }
    &__item {
      font-size: 16px;
      margin: 0;
      &_active {
        transform: scale(1.1);
      }
    }
    &__body {
      padding: 0 30px;
    }
  }
}

@media (max-width: 440px) {
  .cart-total {
    &__warning {
      text-align: center;
    }
    &__price {
      padding: 0 10px;
      flex-direction: column;
      button {
        margin-top: 10px;
      }
    }
    &__payment {
      justify-content: center;
      padding: 0 10px;
    }
  }
}