.cart-checkbox {
  display: block;
  position: relative;
  margin-bottom: 12px;
  font-family: 'Roboto Mono', sans-serif;
  cursor: pointer;
  font-size: 16px;
  line-height: 18px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: flex;
  align-items: center;

  &__text {
    opacity: 0.6;
  }

  &__input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
    &:checked ~ .cart-checkbox__checkmark:after {
      display: block;
    }
  }

  &__checkmark {
    display: block;
    margin-right: 10px;
    top: 0;
    left: 0;
    border-radius: 100%;
    border: 1px solid #000;
    height: 14px;
    width: 14px;
    background-color: #fff;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      display: none;
      background-color: #000;
      top: 2px;
      left: 2px;
      width: 8px;
      height: 8px;
      border-radius: 100%;
    }
  }
}
