.cart {
  &__btn {
    padding: 0;
    width: 60px;
    height: 60px;
    border: none;
    background: #fff url('../../images/cart.svg') center;
    background-size: 60%;
    background-repeat: no-repeat;
    position: fixed;
    right: 30px;
    top: 30px;
    cursor: pointer;
    box-shadow: 0 0 10px 0 rgba(0,0,0,.2);
    border-radius: 100%;
    opacity: 0.8;
    transition: transform 0.2s ease;

    &:hover {
      transform: scale(1.2);
    }
  }

  &__container {
    display: flex;
    justify-content: center;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow-y: scroll;

    &__close {
      position: fixed;
      width: 40px;
      height: 40px;
      display: block;
      right: 20px;
      top: 20px;
      background: url('../../images/close-white.svg') center;
      background-size: 100%;
      background-repeat: no-repeat;
      z-index: 2;
      border: none;
      cursor: pointer;
    }

    &__overlay {
      position: absolute;
      width: 100%;
      height: 150%;
      background: rgba(0, 0, 0, 0.4);
      
    }
  }
}

@media (max-width: 940px) {
  .cart {
    &__container {
      &__close {
        position: absolute;
        top: 10px;
        right: 10px;
        background-image: url('../../images/close.svg');
      }
    }
  }
}

@media (max-width: 768px) {
  .cart {
    &__container {
      &__overlay {
        height: 100%;
      }
    }
    &__btn {
      width: 60px;
      height: 60px;
      bottom: 30px;
      top: auto;
    }
  }
}
