.cart-product {
  display: flex;
  position: relative;
  height: 110px;
  transition: height 0.1s linear;
  overflow: hidden;

  &_remove {
    height: 40px;
  }

  &__remove {
    position: absolute;
    right: 0;
    top: 0;
    width: 24px;
    height: 24px;
    border: none;
    opacity: 0.6;
    cursor: pointer;
    background: transparent url('../../../../images/close.svg') center;
    transition: opacity 0.1s ease;

    &:hover {
      opacity: 1;
    }
  }

  &__img {
    height: 110px;
  }

  &__info {
    padding-left: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;

    &__title {
      margin-bottom: 5px;
      width: calc(100% - 30px);
    }

    &__main {
      font-family: 'Roboto Slab', sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 19px;
      margin-bottom: 8px;
      width: calc(95% - 24px);
    }
  }

  &__timer {
    font-family: 'Roboto Mono', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    color: #7b7b7b;

    &__left {
      display: flex;
      align-items: center;
    }

    &__counter {
      color: #ff3636;
      display: inline-block;
      margin-right: 15px;
      font-size: 16px;
      width: 24px;
      min-width: 24px;
      height: 24px;
      border: 2px solid #ff3636;
      font-weight: bold;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      border-radius: 100%;
    }

    &__return {
      border: none;
      background: transparent;
      opacity: 0.6;
      transition: opacity 0.1s ease;
      cursor: pointer;

      &:hover {
        opacity: 0.8;
      }
    }
  }
}

@media (max-width: 940px) {
  .cart-product {
    &__timer {
      font-size: 13px;
    }
  }
}

@media (max-width: 768px) {
  .cart-product {
    &__timer {
      font-size: 13px;
    }
  }
}

@media (max-width: 440px) {
  .cart-product {
    &__info {
      &__cost,
      &__title {
        font-size: 14px;
        line-height: 14px;
      }
    }
    &__remove {
      right: 10px;
    }
    &__timer {
      padding: 0 10px;
      align-items: flex-start;
      &__left {
        padding-right: 20px;
      }
    }
  }
}
