select,
button {
  &:focus {
    outline: none;
  }
}

* {
  box-sizing: border-box;
}

body {
  &.no-scroll {
    overflow: hidden;
  }
}

.story {
  h1 {
    padding-top: 30px;
    text-align: center;
    max-width: 500px;
    width: 100%;
    margin: 0 auto;
    font-family: 'Roboto Slab', serif;
    font-style: normal;
    font-weight: normal;
    font-size: 28px;
    line-height: 32px;
  }

  h2 {
    font-family: 'Roboto Slab', serif;
    font-size: 24px;
    line-height: 28px;
  }

  max-width: 700px;
  width: 100%;
  margin: 0 auto;
  font-size: 20px;
  line-height: 1.55;
  font-family: 'PT Serif', serif;
  font-weight: 300;
  color: #000;

  @media (max-width: 768px) {
    padding: 0 10px 60px;
  }
}
