.cart-input {
  width: 100%;
  margin: 15px auto;
  position: relative;

  &__label {
    font-family: 'Roboto Mono', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 18px;
    position: absolute;
    top: 5px;
    left: 0;
    cursor: text;
    transition: transform 0.25s ease;
    opacity: 0.6;
    transform-origin: 0 50%;
    z-index: 1;
  }

  &__input {
    padding: 5px 0;
    background: transparent;
    border: none;
    border-bottom: 1px solid #000;
    align-self: flex-start;
    font-family: 'Roboto Mono', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 18px;
    margin-bottom: 10px;
    width: 100%;
    position: relative;
    z-index: 2;

    &_uppercase {
      text-transform: uppercase;
    }

    &:focus,
    &:valid {
      outline: none;
      & + .cart-input {
        &__label {
          transform: translateY(-15px) scale(0.7);
        }
      }
    }
  }

  &_warning {
    .cart-input {
      &__input {
        border-color: #ff0000;
      }
      &__label {
        color: #ff0000;
      }
    }
  }
}

@media (max-width: 768px) {
  .cart-input {
    &__label,
    &__input {
      font-size: 14px;
    }
  }
}
