.cart-textarea {
  width: 100%;
  position: relative;
  margin: 15px 0;

  &__input {
    width: 100%;
    border: 1px solid rgba(0, 0, 0, 0.6);
    background: transparent;
    border-radius: 3px;
    padding: 10px 8px;
    resize: none;
    font-family: 'Roboto Mono', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;

    &:focus,
    &:valid {
      outline: none;

      &+ .cart-textarea {
        &__label {
          transform: translateY(-15px) scale(0.7) ;
        }
      }
    }
  }

  &__label {
    left: 5px;
    top: 5px;
    position: absolute;
    font-family: 'Roboto Mono', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    background: rgb(247, 247, 247);
    padding: 0 3px;
    color: rgba(0, 0, 0, 0.6);
    cursor: text;
    transform-origin: 0 50%;
    transition: transform .25s ease;
  }

  &_warning {
    .cart-textarea {
      &__input {
        border-color: #ff0000;
      }
      &__label {
        color: #ff0000;
      }
    }
  }
}

@media (max-width: 768px) {
  .cart-textarea {
    &__label,
    &__input {
      font-size: 13px;
      line-height: 14px;
    }
  }
}